import { Injectable } from '@angular/core';
import { Http, Request, RequestOptionsArgs, Response, RequestOptions, ConnectionBackend, Headers } from '@angular/http';
import 'rxjs/Rx';
import { Observable } from 'rxjs/Observable';
declare let jQuery: any;
@Injectable()
export class HttpService extends Http {
  status = {
    "status.400": "",
    "status.401": "",
    "status.403": "",
    "status.404": "",
    "status.405": "",
    "status.406": "",
    "status.407": "",
    "status.408": "",
    "status.409": "",
    "status.410": "",
    "status.411": "",
    "status.412": " false",
    "status.413": "",
    "status.414": " URI  URL ",
    "status.415": "",
    "status.416": "HTTP  {0}",
    "status.500": "",
    "status.501": "",
    "status.503": "()"
  };
  constructor(backend: ConnectionBackend, defaultOptions: RequestOptions) {
    super(backend, defaultOptions);
  }
  request(url: string | Request, options?: RequestOptionsArgs): Observable<Response> {
    //http
    typeof url == 'string' ? url : url.url;
    return this.intercept(super.request(url, options));
  }
  get(url: string, options?: RequestOptionsArgs): Observable<Response> {
    return this.intercept(super.get(url, options)).map(res => res.json());
  }
  post(url: string, body: string, options?: RequestOptionsArgs): Observable<Response> {
    return this.intercept(super.post(url, body, this.getRequestOptionArgs(options))).map(res => res.json());
  }
  put(url: string, body: string, options?: RequestOptionsArgs): Observable<Response> {
    return this.intercept(super.put(url, body, this.getRequestOptionArgs(options)));
  }
  delete(url: string, options?: RequestOptionsArgs): Observable<Response> {
    return this.intercept(super.put(url, this.getRequestOptionArgs(options)));
  }
  getRequestOptionArgs(options?: RequestOptionsArgs): RequestOptionsArgs {
    if (options == null) {
      options = new RequestOptions();
    }
    if (options.headers == null) {
      options.headers = new Headers();
    }
    options.headers.append('Content-Type', 'application/x-www-form-urlencoded');
    return options;
  }
  intercept(observable: Observable<Response>): Observable<Response> {
    return observable.catch((err, source) => {
      jQuery('ion-loading').remove();
      // if (err.status < 200 || err.status >= 300) {
      //   return Observable.empty();
      // } else {
      //   return Observable.throw(err);
      // }
      return Observable.throw(err);
    });
    //return observable;
  }
}


